"use client"

import { cn } from "@/lib/utils"
import { AppSidebar } from "@/components/app-sidebar"
import { useSidebar } from "@/components/sidebar-provider"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Plus, TrendingUp, TrendingDown, Wallet, Sparkles } from "lucide-react"

export default function DashboardPage() {
  const { isCollapsed } = useSidebar()

  return (
    <div className="dark flex min-h-screen">
      <AppSidebar />
      <main
        className={cn(
          "ml-64 flex-1 overflow-auto bg-background transition-all duration-300",
          isCollapsed ? "ml-16" : "ml-64",
        )}
      >
        <div className="p-8">
          {/* Header */}
          <div className="mb-8 flex items-center justify-between">
            <div>
              <h1 className="mb-1 text-2xl font-semibold text-foreground">Welcome back, Suraj!</h1>
              <p className="text-sm text-muted-foreground">{"Here's your financial overview for October 2025"}</p>
            </div>
            <Button className="bg-primary text-primary-foreground hover:bg-primary/90">
              <Plus className="mr-2 h-4 w-4" />
              Add Transaction
            </Button>
          </div>

          {/* Metric Cards */}
          <div className="mb-8 grid gap-4 md:grid-cols-2 lg:grid-cols-4">
            <Card className="border-border bg-card">
              <CardHeader className="flex flex-row items-center justify-between pb-2">
                <CardTitle className="text-sm font-medium text-card-foreground">Total Income</CardTitle>
                <TrendingUp className="h-4 w-4 text-primary" />
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold text-card-foreground">$2,800</div>
                <p className="text-xs text-muted-foreground">
                  <span className="text-primary">+12%</span> from last month
                </p>
              </CardContent>
            </Card>
            <Card className="border-border bg-card">
              <CardHeader className="flex flex-row items-center justify-between pb-2">
                <CardTitle className="text-sm font-medium text-card-foreground">Total Expenses</CardTitle>
                <TrendingDown className="h-4 w-4 text-destructive" />
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold text-card-foreground">$130.74</div>
                <p className="text-xs text-muted-foreground">
                  <span className="text-destructive">+3%</span> from last month
                </p>
              </CardContent>
            </Card>
            <Card className="border-border bg-card">
              <CardHeader className="flex flex-row items-center justify-between pb-2">
                <CardTitle className="text-sm font-medium text-card-foreground">Balance</CardTitle>
                <Wallet className="h-4 w-4 text-cyan-400" />
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold text-card-foreground">$2,669.26</div>
                <p className="text-xs text-muted-foreground">Available to spend</p>
              </CardContent>
            </Card>
            <Card className="border-border bg-card">
              <CardHeader className="flex flex-row items-center justify-between pb-2">
                <CardTitle className="text-sm font-medium text-card-foreground">Savings Rate</CardTitle>
                <Sparkles className="h-4 w-4 text-primary" />
              </CardHeader>
              <CardContent>
                <div className="text-2xl font-bold text-primary">95%</div>
                <p className="text-xs text-muted-foreground">Great job saving!</p>
              </CardContent>
            </Card>
          </div>

          {/* Charts Row */}
          <div className="mb-8 grid gap-6 lg:grid-cols-2">
            {/* Income vs Expenses Chart */}
            <Card className="border-border bg-card">
              <CardHeader>
                <CardTitle className="text-card-foreground">Income vs Expenses</CardTitle>
                <CardDescription>Last 6 months overview</CardDescription>
              </CardHeader>
              <CardContent>
                <div className="flex h-64 items-end justify-around gap-2">
                  {[
                    { month: "Jul", income: 280, expense: 200 },
                    { month: "Aug", income: 290, expense: 210 },
                    { month: "Sep", income: 275, expense: 200 },
                    { month: "Oct", income: 300, expense: 220 },
                    { month: "Nov", income: 285, expense: 210 },
                    { month: "Dec", income: 310, expense: 150 },
                  ].map((data, i) => (
                    <div key={i} className="flex flex-1 flex-col items-center gap-2">
                      <div className="flex w-full items-end justify-center gap-1">
                        <div
                          className="w-full rounded-t bg-primary"
                          style={{ height: `${(data.income / 310) * 200}px` }}
                        />
                        <div
                          className="w-full rounded-t bg-destructive"
                          style={{ height: `${(data.expense / 310) * 200}px` }}
                        />
                      </div>
                      <span className="text-xs text-muted-foreground">{data.month}</span>
                    </div>
                  ))}
                </div>
                <div className="mt-4 flex justify-center gap-4 text-xs">
                  <div className="flex items-center gap-2">
                    <div className="h-3 w-3 rounded bg-destructive" />
                    <span className="text-muted-foreground">expenses</span>
                  </div>
                  <div className="flex items-center gap-2">
                    <div className="h-3 w-3 rounded bg-primary" />
                    <span className="text-muted-foreground">income</span>
                  </div>
                </div>
              </CardContent>
            </Card>

            {/* Spending by Category */}
            <Card className="border-border bg-card">
              <CardHeader>
                <CardTitle className="text-card-foreground">Spending by Category</CardTitle>
                <CardDescription>{"This month's breakdown"}</CardDescription>
              </CardHeader>
              <CardContent>
                <div className="flex h-64 items-center justify-center">
                  <div className="relative h-48 w-48">
                    <svg className="h-full w-full -rotate-90" viewBox="0 0 100 100">
                      <circle
                        cx="50"
                        cy="50"
                        r="40"
                        fill="none"
                        stroke="#4ade80"
                        strokeWidth="20"
                        strokeDasharray="178 251"
                        strokeLinecap="round"
                      />
                      <circle
                        cx="50"
                        cy="50"
                        r="40"
                        fill="none"
                        stroke="#60a5fa"
                        strokeWidth="20"
                        strokeDasharray="43 251"
                        strokeDashoffset="-178"
                        strokeLinecap="round"
                      />
                      <circle
                        cx="50"
                        cy="50"
                        r="40"
                        fill="none"
                        stroke="#34d399"
                        strokeWidth="20"
                        strokeDasharray="30 251"
                        strokeDashoffset="-221"
                        strokeLinecap="round"
                      />
                    </svg>
                  </div>
                </div>
                <div className="mt-4 space-y-2">
                  <div className="flex items-center justify-between">
                    <div className="flex items-center gap-2">
                      <div className="h-3 w-3 rounded bg-[#4ade80]" />
                      <span className="text-sm text-muted-foreground">Food 71%</span>
                    </div>
                  </div>
                  <div className="flex items-center justify-between">
                    <div className="flex items-center gap-2">
                      <div className="h-3 w-3 rounded bg-[#60a5fa]" />
                      <span className="text-sm text-muted-foreground">Transport 17%</span>
                    </div>
                  </div>
                  <div className="flex items-center justify-between">
                    <div className="flex items-center gap-2">
                      <div className="h-3 w-3 rounded bg-[#34d399]" />
                      <span className="text-sm text-muted-foreground">Entertainment 12%</span>
                    </div>
                  </div>
                </div>
              </CardContent>
            </Card>
          </div>

          {/* Bottom Row */}
          <div className="grid gap-6 lg:grid-cols-3">
            {/* Balance Trend */}
            <Card className="border-border bg-card lg:col-span-2">
              <CardHeader>
                <CardTitle className="text-card-foreground">Balance Trend</CardTitle>
                <CardDescription>Your savings over time</CardDescription>
              </CardHeader>
              <CardContent>
                <div className="h-48">
                  <svg className="h-full w-full" viewBox="0 0 600 200">
                    <defs>
                      <linearGradient id="balanceGradient" x1="0%" y1="0%" x2="0%" y2="100%">
                        <stop offset="0%" stopColor="#4ade80" stopOpacity="0.3" />
                        <stop offset="100%" stopColor="#4ade80" stopOpacity="0" />
                      </linearGradient>
                    </defs>
                    <path
                      d="M 0 180 L 100 170 L 200 165 L 300 160 L 400 155 L 500 100 L 600 20"
                      fill="none"
                      stroke="#4ade80"
                      strokeWidth="2"
                    />
                    <path
                      d="M 0 180 L 100 170 L 200 165 L 300 160 L 400 155 L 500 100 L 600 20 L 600 200 L 0 200 Z"
                      fill="url(#balanceGradient)"
                    />
                    <circle cx="600" cy="20" r="4" fill="#4ade80" />
                  </svg>
                </div>
              </CardContent>
            </Card>

            {/* Recent Transactions */}
            <Card className="border-border bg-card">
              <CardHeader>
                <CardTitle className="text-card-foreground">Recent Transactions</CardTitle>
                <CardDescription>Last 5 activities</CardDescription>
              </CardHeader>
              <CardContent>
                <div className="space-y-4">
                  {[
                    { name: "Grocery Store", date: "Oct 10", category: "Food", amount: -85.5 },
                    { name: "Salary Deposit", date: "Oct 8", category: "Income", amount: 2800 },
                    { name: "Netflix", date: "Oct 7", category: "Entertainment", amount: -15.99 },
                    { name: "Uber", date: "Oct 6", category: "Transport", amount: -22.5 },
                    { name: "Coffee Shop", date: "Oct 5", category: "Food", amount: -6.75 },
                  ].map((transaction, i) => (
                    <div key={i} className="flex items-center justify-between">
                      <div className="flex-1">
                        <div className="text-sm font-medium text-card-foreground">{transaction.name}</div>
                        <div className="text-xs text-muted-foreground">
                          {transaction.date} â€¢ {transaction.category}
                        </div>
                      </div>
                      <div
                        className={cn(
                          "text-sm font-medium",
                          transaction.amount > 0 ? "text-primary" : "text-foreground",
                        )}
                      >
                        {transaction.amount > 0 ? "+" : ""}${Math.abs(transaction.amount).toFixed(2)}
                      </div>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>
          </div>
        </div>
      </main>
    </div>
  )
}
